<template>
<div class="modalBack">
    <div class="modal">
        <div class="modalMenuTop">
            <img src="../icons/iconmonstr-x-mark-4.svg" class="closeBtnModal" @click="addApps">
        </div>
        <h3>Upravit oblíbené aplikace</h3>
        <div class="modalMid">

            

            
            <!-- <label for="myApps">Moje aplikace</label> -->
            <div class="myApps" name="myApps">

                <div class="myAppsDiv"  v-for="app in apps"> 
                    <!-- <input type="checkbox" v-if="app.fav==true" checked v-model="checkedApp"> -->

                    <div class="appNameModal"  >
                        <!-- <img :src="app.img" class="imgApp"> -->
                        <div class="nameAppDiv" >
                            <div class="inputAppDiv">
                                <input type="checkbox" v-if="app.fav==true" checked v-model="checkedApp">
                            </div>
                            
                            <div class="appImgDiv">
                                <img :src="app.img" class="imgApp">
                            </div>
                            
                            <div class="appTitleDiv">
                                <p>{{app.name}}</p>
                            </div>
                            

                            <div class="selectAppDiv">

                            
                                <select class="select" v-if="app.size==1">
                                    <option value="1">Mala</option>
                                    <option value="2">Stredni</option>
                                    <option value="3">Velka</option>
                                </select>

                                <select class="select" v-if="app.size==2">
                                    <option value="2">Stredni</option>
                                    <option value="1">Mala</option>
                                    <option value="3">Velka</option>
                                </select>

                                <select class="select" v-if="app.size==3">
                                    <option value="3">Velka</option>
                                    <option value="1">Mala</option>
                                    <option value="2">Stredni</option>
                                </select>

                            </div>

                        </div>

                        <div class="dropApp" v-if="showDrop">
                            <input type="color" class="colorPicker">
                            <p>Barva aplikace</p>
                        </div>
                        
                    </div>
                    
                </div>
                
            </div>
            
            <div class="allApps">
                <div class="myAppsDiv"  v-for="app in unFavApp"> 
                    <input type="checkbox" v-if="app.fav==false">

                    <div class="appNameModal">
                        <img :src="app.img" class="imgApp">
                        <div class="nameAppDiv">
                            
                            <div class="appTitleDiv">
                                <p>{{app.name}}</p>
                            </div>

                            <select class="select" v-if="app.size==1">
                                <option value="1">Mala</option>
                                <option value="2">Stredni</option>
                                <option value="3">Velka</option>
                            </select>

                            <select class="select" v-if="app.size==2">
                                <option value="2">Stredni</option>
                                <option value="1">Mala</option>
                                <option value="3">Velka</option>
                            </select>

                            <select class="select" v-if="app.size==3">
                                <option value="3">Velka</option>
                                <option value="1">Mala</option>
                                <option value="2">Stredni</option>
                            </select>

                        </div>
                        
                    </div>
                    
                </div>
            </div>
        
        </div>
        <button class="confirmBtn" @click="confirm">Potvrdit</button>
    </div>
</div>
