<script>


export default {
    props:['turned', 'apps', 'unFavApp', 'addApps'],
    name: 'Modal',
    checkedApp: '',

    
    data(){
        return{
            clicked: false,
            showDrop: false
        }
    },
    methods:{
        closeModal(){
            if(!this.turned){
                this.turned = true
                console.log(this.turned)
            }else{
                this.turned = false
                console.log(this.turned)
            } 
        },
        close(){
            if(!this.clicked){
                this.clicked = false
                console.log(this.clicked)
            } else if(this.clicked){
                this.clicked = true
                console.log(this.clicked)
            }
        },
        openDrop(){
            if(!this.showDrop){
                this.showDrop = true
            } else if(this.showDrop){
                this.showDrop = false
            }
        }
    }
}
</script>

