<template>
  <div class="modalNewsDiv">
      <div class="modalNews">
        <div class="topMod">
            <img src="../icons/iconmonstr-x-mark-4.svg" class="closeBtnModal" @click="addNews">
        </div>
        <div class="title">Pridat novinku</div>
            <input type="text" placeholder="Nadpis.." class="titleInput" v-model="title">

            <div class="inputMidDiv">
                <input type="date" class="dateInput" v-model="date">
                <input type="text" placeholder="Odkaz.." class="urlInput"> 
            </div>

            <!-- <input type="text" class="contentInput"> -->

            <textarea class="textArea" v-model="textContent"></textarea>

            <div class="submitDiv">
                <!-- <input type="file" class="fileBtn"> -->
                <button class="submitBtn" @click="pushNews">Pridat</button>
            </div>
      </div>
  </div>
</template>

<script>



export default {
 name: 'ModalNews',
 props:['addNewsShown', 'addNews', 'newsAll'],
 components:{
  
    
 },
 data(){
    return{
        title: '',
        textContent: '',
        date: ''
    }
 },
 methods:{
     pushNews: function(){
         if(this.title=='' || this.textContent==''){
             alert('Nadpis nebo popis nesmi byt prazdny')
         } else{
            this.newsAll.unshift({
             title: this.title,
             text: this.textContent,
             date: this.date
         })
            this.newsAll.pop()
        }
     }
 }
}
</script>

<style>
.modalNews{
    height: 25rem;
    width: 50rem;
    background-color: rgb(241, 241, 240);
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: column;
    resize: both;
    overflow: auto;
    position: absolute;

}

.modalNewsDiv{
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    width: 30vw;
}

.topMod{
    height: 2rem;
    width: 100%;
    background-color: rgb(63, 63, 63);
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

.title{
    font-family: 'Inter', sans-serif;
    display: flex;
    width: 80%;
    margin-top: 10px;
    font-weight: bold;
}

.contentInput{
    width: 80%;
    height: 50%;
    text-align: start;
}

.inputMidDiv{
    display: flex;
    align-items: center;
    width: 80%;
    margin-bottom: 10px;
    justify-content: space-between;
}

.urlInput{
    width: 75%;
    border-radius: 5px;
    border: 1px solid rgb(184, 183, 183);
    outline: none;
}

.titleInput{
    width: 80%;
    margin-top: 10px;
    margin-bottom: 10px;
    border-radius: 5px;
    border: 1px solid rgb(184, 183, 183);
    outline: none;
}

.textArea{
    width: 80%;
    height: 50%;
    border-radius: 5px;
    border: 1px solid rgb(184, 183, 183);
    outline: none;
}

.dateInput{
    border-radius: 5px;
    border: 1px solid rgb(184, 183, 183);
    outline: none;
}

.submitBtn{
    margin-top: 15px;
    padding: 5px;
    width: 100px;
    background-color: rgba(63, 63, 63, 0.651);
    color: white;
    border: none;
    outline: none;
    border-radius: 5px;
    cursor: pointer;
    font-family: 'Inter', sans-serif;
}

.submitBtn:hover{
    transform: scale(1.02);
    background-color: rgba(63, 63, 63, 0.733);
}

</style>